import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { LocationStrategy, HashLocationStrategy } from '@angular/common';
import { AppRoutingModule } from './app-routing.module';

import { AppComponent } from './app.component';
import { HomeComponent } from './components/home/home.component';

import { CarService } from './service/carservice';
import { CountryService } from './service/countryservice';
import { EventService } from './service/eventservice';
import { NodeService } from './service/nodeservice';
import { NzCodeBoxModule } from './share/nz-codebox/nz-codebox.module';
import { NzHighlightModule } from './share/nz-highlight/nz-highlight.module';
import { CustomComponentsModule } from "app/custom-components/custom-components.module";
import { API } from "app/custom-components/services/api";
import { NzGridModule } from "app/showcase/share/nz-grid/nz-grid.module";
import { NzToolTipModule } from "app/showcase/share/nz-tooltip/nz-tooltip.module";

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    AppRoutingModule,
    HttpModule,
    BrowserAnimationsModule,
    CustomComponentsModule,
    NzCodeBoxModule,
    NzGridModule,
    NzToolTipModule,
    NzHighlightModule
  ],
  providers: [
    { provide: LocationStrategy, useClass: HashLocationStrategy },
    API,
    CarService, CountryService, EventService, NodeService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



