
/*tslint:disable*/
import { Injectable } from "@angular/core";
import { API } from "app/custom-components/services/api";
@Injectable()
export class AreaService {
    cacheArea: any = {};
    constructor(public api: API) { }

    selectBoxHandler(): any {
        let api = this.api;
        return (boxs, select) => {
            // Tab
            let title: string;
            switch (boxs.length) {
                case 0: title = "//"; break;
                case 1: title = "/"; break;
                case 2: title = "/"; break;
                case 3: title = "/"; break;
                default: title = "/";
            }
            // 
            if (select) {
                let cacheArea = this.getCache(select.value);
                // 
                if (cacheArea.length) {
                    let data = [];
                    boxs.push({
                        title: title,
                        data: data
                    });
                    for (let area of cacheArea) {
                        if (area.code === "000000000000") {
                            continue;
                        }
                        data.push({
                            label: area.name,
                            value: area.code,
                            id: area.id,
                            level: area.level,
                            end: boxs.length == 3
                        });
                    }
                    return;
                }
                api.call("CommonController.findAreasByParent", {
                    "code": select.value
                }).ok(json => {
                    let data = [];
                    boxs.push({
                        title: title,
                        data: data
                    });
                    let result: any[] = json.result;
                    this.setCache(select.value, result); // 
                    for (let area of result) {
                        if (area.code === "000000000000") {
                            continue;
                        }
                        data.push({
                            label: area.name,
                            value: area.code,
                            id: area.id,
                            level: area.level,
                            end: boxs.length == 3
                        });
                    }
                });
            } else {
                let cacheArea = this.getCache("000000000000");
                // 
                if (cacheArea.length) {
                    let data = [];
                    boxs.push({
                        title: title,
                        data: data
                    });
                    for (let area of cacheArea) {
                        if (area.code === "000000000000") {
                            continue;
                        }
                        data.push({
                            label: area.name,
                            level: area.level,
                            id: area.id,
                            value: area.code
                        });
                    }
                    return;
                }
                api.call("CommonController.findAreasByParent", {
                    "code": "000000000000"
                }).ok(json => {
                    let data = [];
                    boxs.push({
                        title: title,
                        data: data
                    });
                    let result: any[] = json.result;
                    this.setCache("000000000000", result); // 
                    for (let area of result) {
                        if (area.code === "000000000000") {
                            continue;
                        }
                        data.push({
                            label: area.name,
                            level: area.level,
                            id: area.id,
                            value: area.code
                        });
                    }
                });
            }
        };
    }
    /**
     * code
     */
    selectBoxLabelHandler(): any {
        return (selectBox, code) => {
            this.api.call("CommonController.generateLabelForArea", code).ok(json => {
                selectBox.label = json.result;
            });

        };
    }
    /**
     * 
     * @param code 
     * @param data 
     */
    setCache(code: string, data) {
        let cacheAreaStr = sessionStorage.getItem('cacheAreaData') || '{}';
        let cacheArea = JSON.parse(cacheAreaStr);
        cacheArea[code] = data;
        let newCache = JSON.stringify(cacheArea);
        sessionStorage.setItem('cacheAreaData', newCache);
    }
    /**
     *
     * @param code 
     */
    getCache(code: string) {
        let cacheAreaStr = sessionStorage.getItem('cacheAreaData') || '{}';
        let cacheArea = JSON.parse(cacheAreaStr);
        return cacheArea[code] || [];
    }
}



