import {Component, forwardRef, Input, OnInit, EventEmitter, Output} from "@angular/core";
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from "@angular/forms";
import { API } from "app/custom-components/services/api";


let noop = () => {
};

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => AbnormalMoverComponent),
    multi: true
};

@Component({
    templateUrl: "abnormal-mover-select.component.html",
    styleUrls: ["abnormal-mover-select.component.css"],
    selector: "abnormal-mover-select",
    providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class AbnormalMoverComponent implements ControlValueAccessor,OnInit {
    abnormalMover: any[];

    @Input()
    defaultLabel: string = "";

    @Input()
    multiSelect: boolean = false;

    @Input()
    width: string = "";

    @Input()
    height: string = "";

    /*    @Input()
     waybillId: string;*/

    @Input()
    styleClass: String;

    @Output()
    onChange: EventEmitter<any> = new EventEmitter<any>();

    @Output()
    onFocus: EventEmitter<any> = new EventEmitter<any>();

    @Output()
    onBlur: EventEmitter<any> = new EventEmitter<any>();


    public onTouchedCallback: () => void = noop;
    public onChangeCallback: (_: any) => void = noop;
    public innerValue;


    constructor(public api: API) {
    }

    ngOnInit() {
        this.abnormalMover = [];
        //if(this.waybillId){
        this.query();
        /*}*/
    }

    // 
    get value(): any {
        return this.innerValue;
    };

    // 
    set value(v: any) {
        if (v !== this.innerValue) {
            this.innerValue = v;
            this.onChangeCallback(v);
        }
    }

    // 
    writeValue(value: any) {
        if (value !== this.innerValue) {
            this.innerValue = value;
        }
    }

    // 
    registerOnChange(fn: any) {
        this.onChangeCallback = fn;
    }

    // 
    registerOnTouched(fn: any) {
        this.onTouchedCallback = fn;
    }

    onChangeHandler(event) {
        this.onChange.emit(event);
    }

    onFocusHandler(event) {
        this.onFocus.emit(event);
    }

    onBlurHandler(event) {
        this.onBlur.emit(event);
    }

    //query(waybillId: string="") {
    query() {
        let queryParams = {
            "code": "yztpt"
        }
        this.api.call("CompanyController.departUserQueryByCode", queryParams).ok((json) => {
            let result: any = json.result || {};
            //console.info(json);
            this.abnormalMover = result || [];
            //console.info(result);
            this.abnormalMover.forEach((abnMov) => {
                abnMov.label = abnMov["realName"] + " " + abnMov["mobile"];
                abnMov.value = abnMov["id"];
            });
        })
    }
}



