/**
 * 
 */
import {Injectable} from "@angular/core";
import { API } from "app/custom-components/services/api";

@Injectable()
export class AbnormalTypeService{

    constructor(public api:API){}

    selectBoxHandler():any {
        let api = this.api;
        return (boxs,select)=>{
            // Tab
            let title:string;
            switch (boxs.length){
                case 0:title="";break;
                case 1:title="";break;
            }

            // 
            if(select){
                api.call("AbnormalTypeController.abnormalTypeFind",{
                    first:0,
                    rows:9999
                },{
                    "parentId":select.value
                }).ok(json=>{
                    let data = [];
                    boxs.push({
                        title:title,
                        data:data
                    });
                    let result:any[] = json.result.content;
                    for(let type of result){
                        data.push({
                            label:type.name,
                            value:type.id,
                            end:boxs.length==2
                        });
                    }
                });
            }
            else {
                api.call("AbnormalTypeController.abnormalTypeFind",{
                    first:0,
                    rows:9999
                },{}).ok(json=>{
                    let data = [];
                    boxs.push({
                        title:title,
                        data:data
                    });
                    let result:any[] = json.result.content;
                    for(let type of result){
                        data.push({
                            label:type.name,
                            value:type.id
                        });
                    }
                }).fail(json=>{
                    ////console.log(json);
                });
            }
        };
    }

    selectBoxLabelHandler():any{
        return (selectBox,code)=>{
            this.api.call("AbnormalTypeController.generateLabelForAbnormalType",code).ok(json=>{
                selectBox.label = json.result;
            });

        };
    }
}



