import { Component, forwardRef, Input, Output, EventEmitter, NgModule } from "@angular/core";
import { ControlValueAccessor, NG_VALUE_ACCESSOR, FormsModule } from "@angular/forms";
import { AbnormalTypeService } from "./abnormal-type.service";
import { CommonModule } from "@angular/common";
import { UISelectBoxModule } from "app/custom-components/select-box/select-box";


let noop = () => {
};

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => ExceptionSelectComponent),
    multi: true
};

@Component({
    selector: "exception-select",
    templateUrl: "exception-select.component.html",
    styleUrls: ["exception-select.component.css"],
    providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class ExceptionSelectComponent implements ControlValueAccessor {
    /**
     * 
     */
    @Input()
    label: any;

    @Output()
    onChange: EventEmitter<any> = new EventEmitter<any>();

    public onTouchedCallback: () => void = noop;
    public onChangeCallback: (_: any) => void = noop;
    public innerValue;

    dataHandler: Function = this.abnormalTypeService.selectBoxHandler();
    // 
    get value(): any {
        return this.innerValue;
    };

    // 
    set value(v: any) {
        if (v !== this.innerValue) {
            this.innerValue = v;
            this.onChangeCallback(v);
        }
    }

    constructor(public abnormalTypeService: AbnormalTypeService) {
    }

    // 
    writeValue(value: any) {
        if (value !== this.innerValue) {
            this.innerValue = value;
        }
    }

    // 
    registerOnChange(fn: any) {
        this.onChangeCallback = fn;
    }

    // 
    registerOnTouched(fn: any) {
        this.onTouchedCallback = fn;
    }

    /**
     * 
     * @param val
     */
    onValueChange(val) {
        this.onChange.emit(val);
    }

}

@NgModule({
    declarations: [
        ExceptionSelectComponent,
    ],
    exports: [ExceptionSelectComponent],
    imports: [
        CommonModule,
        FormsModule,
        UISelectBoxModule
    ]
})
export class ExceptionSelectModule { }



