import { Component, OnInit } from '@angular/core';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { API } from "app/custom-components/services/api";

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css'],
    animations: [
        trigger('overlayState', [
            state('hidden', style({
                opacity: 0
            })),
            state('visible', style({
                opacity: 1
            })),
            transition('visible => hidden', animate('400ms ease-in')),
            transition('hidden => visible', animate('400ms ease-out'))
        ])
    ],
})
export class AppComponent implements OnInit {


    menuActive: boolean;

    activeMenuId: string;

    loginUser: string;

    constructor(public api: API) {

    }
    ngOnInit(): void {
        this.checkUserLogin().then(isLogin => {
            if (isLogin) {
                let curUser = localStorage.getItem('currentUser');
                if (curUser) {
                    let user = JSON.parse(curUser);
                    this.loginUser = user['realName'];
                }
            } else {
                this.loginUser = null;
            }
        }).catch(()=>{
            this.loginUser = null;
        })

    }

    changeTheme(event: Event, theme: string) {
        let themeLink: HTMLLinkElement = <HTMLLinkElement>document.getElementById('theme-css');
        themeLink.href = 'assets/components/themes/' + theme + '/theme.css';
        event.preventDefault();
    }

    onMenuButtonClick(event: Event) {
        this.menuActive = !this.menuActive;
        event.preventDefault();
    }

    /**
   * 
   * @returns {boolean}
   */
    checkUserLogin(): any {
        try {
            return new Promise((resolve, reject) => {
                this.api.call("SecurityController.currentUser").ok(json => {
                    if (json.result) {
                        localStorage.setItem('currentUser', JSON.stringify(json.result.attributes));
                        resolve(true)
                    } else {
                        resolve(false)
                    }
                }).fail((err) => {
                    localStorage.clear();
                    reject(false);
                });
            });
        } catch (e) {
            localStorage.clear();
        }
    }
}



