// ---------------------------------------------------------
// | Imports
// ---------------------------------------------------------

// Common of angular
import { NgModule, ModuleWithProviders } from '@angular/core';
//share module
import { CommonModule } from "@angular/common";
import { FormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";
// primeng
import { DataTableModule } from "app/components/datatable/datatable";
import { TreeModule } from "app/components/tree/tree";
import { ButtonModule } from "app/components/button/button";
import { MultiSelectModule } from "app/components/multiselect/multiselect";
import { DialogModule } from "app/components/dialog/dialog";
import { DropdownModule } from "app/components/dropdown/dropdown";
import { PickListModule } from "app/components/picklist/picklist";

//primeng-ext
import { AutoCompleteModule } from "./_primeng-ext/autocomplete";
import { FileUploadModule } from "./_primeng-ext/fileupload";
import { UICalendarModule } from "./_primeng-ext/calendar/calendar";

// Modules
import { AreaSelectComponent } from "app/custom-components/area-select/area-select.component";
import { AbnormalMoverComponent } from "app/custom-components/abnormal-mover/abnormal-mover-select.component";
import { CneeSelectModule } from "app/custom-components/cnee-select/cnee-select.component";
import { DepartmentSelectModule } from "app/custom-components/department-select/department-select.component";
import { WorkersSelectComponent } from "app/custom-components/worker-select/workers-select.component";
import { ShipperSelectComponent } from "app/custom-components/shipper-select/shipper-select.component";
import { EchartsDirective, EchartsModule } from "app/custom-components/echarts/echarts";
import { UIGridModule } from "app/custom-components/grid/grid";
import { UIUpload } from "app/custom-components/upload/upload";
import { UITree } from "app/custom-components/tree/tree";
import { UISelectBoxModule } from "app/custom-components/select-box/select-box";
import { MasterSelectModule } from "app/custom-components/master-select/master-select.component";
import { MetaSelectComponent } from "app/custom-components/meta-select/meta-select.component";
import { RepairGoodsModule } from "app/custom-components/repair-goods-select/repair-goods-select.component";
import { TransportSelectComponent } from "app/custom-components/transport-select/transport-select.component";
import { API } from "app/custom-components/services/api";
import { AreaService } from "./area-select/area.service";
import { CityDropDownComponent } from "./city-dropdown/city-dropdown.component";
import { GoodSelectModule } from "app/custom-components/good-select/good-select.component";
import { ExceptionSelectModule } from "app/custom-components/exception-select/exception-select.component";


// Services


// ---------------------------------------------------------
// | Exports
// ---------------------------------------------------------

// Modules
//primeng-ext
export { AutoCompleteModule } from "./_primeng-ext/autocomplete";
export { FileUploadModule } from "./_primeng-ext/fileupload";
export { UICalendarModule } from "./_primeng-ext/calendar/calendar";

// Components
export { EchartsDirective, EchartsModule } from "app/custom-components/echarts/echarts";
export { AreaSelectComponent } from "app/custom-components/area-select/area-select.component";
export { AbnormalMoverComponent } from "app/custom-components/abnormal-mover/abnormal-mover-select.component";
export { CneeSelectModule } from "app/custom-components/cnee-select/cnee-select.component";
export { DepartmentSelectModule } from "app/custom-components/department-select/department-select.component";
export { WorkersSelectComponent } from "app/custom-components/worker-select/workers-select.component";
export { ShipperSelectComponent } from "app/custom-components/shipper-select/shipper-select.component";
export { GoodSelectModule } from "app/custom-components/good-select/good-select.component";
export { UIGridModule } from "app/custom-components/grid/grid";
export { UIUpload } from "app/custom-components/upload/upload";
export { UITree } from "app/custom-components/tree/tree";
export { UISelectBoxModule } from "app/custom-components/select-box/select-box";
export { MasterSelectModule } from "app/custom-components/master-select/master-select.component";
export { MetaSelectComponent } from "app/custom-components/meta-select/meta-select.component";
export { RepairGoodsComponent } from "app/custom-components/repair-goods-select/repair-goods-select.component";
export { ExceptionSelectModule } from "app/custom-components/exception-select/exception-select.component";

// Services
export { API } from "app/custom-components/services/api";

// Tokens (eg. global services' config)


// ---------------------------------------------------------
// | Root module
// ---------------------------------------------------------

@NgModule({
  imports: [
    HttpModule,
    CommonModule,
    FormsModule,
    DataTableModule,
    TreeModule,
    MultiSelectModule,
    ButtonModule,
    AutoCompleteModule,
    FileUploadModule,
    DialogModule,
    DropdownModule,
    PickListModule,
    CneeSelectModule,
    GoodSelectModule,
    EchartsModule,
    UIGridModule,
    UICalendarModule,
    DepartmentSelectModule,
    MasterSelectModule,
    ExceptionSelectModule,
    UISelectBoxModule
  ],
  declarations: [
    UIUpload,
    AreaSelectComponent,
    AbnormalMoverComponent,
    ShipperSelectComponent,
    WorkersSelectComponent,
    MetaSelectComponent,
    TransportSelectComponent,
    CityDropDownComponent
  ],

  exports: [
    UICalendarModule,
    UIGridModule,
    UIUpload,
    UISelectBoxModule,
    EchartsModule,
    AreaSelectComponent,
    AbnormalMoverComponent,
    CneeSelectModule,
    ShipperSelectComponent,
    DepartmentSelectModule,
    ExceptionSelectModule,
    WorkersSelectComponent,
    MetaSelectComponent,
    TransportSelectComponent,
    CityDropDownComponent,
    GoodSelectModule,
    AutoCompleteModule,
    MasterSelectModule
  ],
  providers: [
    AreaService
  ]
})
export class CustomComponentsModule {

}



