import {Component, OnInit, Input, forwardRef, NgModule} from '@angular/core';
import {NG_VALUE_ACCESSOR, FormsModule} from "@angular/forms";
import { API } from "app/custom-components/services/api";
import { CommonModule } from "@angular/common";
import { AutoCompleteModule } from "app/custom-components/_primeng-ext/autocomplete";

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => GoodSelectComponent),
    multi: true
};

@Component({
    selector: 'good-select',
    templateUrl: 'good-select.component.html',
    styleUrls: ['good-select.component.css'],
    providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class GoodSelectComponent implements OnInit {

    /**
     * 
     */
    suggestions: any[];

    @Input()
    defaultLabel: string = "";
    @Input()
    multiSelect: boolean = false;
    @Input()
    width: string = "";
    @Input()
    height: string = "";
    @Input()
    valueField: string = "";
    @Input()
    styleClass: string;

    public onTouchedCallback: () => () => {};
    public onChangeCallback: (_: any) => () => {};
    public innerValue;

    constructor(public api: API) {
    }

    ngOnInit() {
        this.suggestions = [];
    }
    // 
    get value(): any {
        return this.innerValue;
    };

    // 
    set value(v: any) {
        let vv = v && v.name ? v.name : v;
        this.innerValue = vv;
        if (this.valueField) {
            // let _value=v[this.valueField]?v[this.valueField]:v;
            this.onChangeCallback(v[this.valueField]);
        } else {
            this.onChangeCallback(vv);
        }
    }

    // 
    writeValue(value: any) {
        if (value !== this.innerValue) {
            this.innerValue = value;
        }
    }

    // 
    registerOnChange(fn: any) {
        this.onChangeCallback = fn;
    }

    // 
    registerOnTouched(fn: any) {
        this.onTouchedCallback = fn;
    }

    /**
     * 
     * @param $event
     */
    queryData($event: any) {
        let value = $event.query;

        let pageParms = {"first": 0, "rows": 9999};
        this.api.call("abnormalOtherHandleController.waybillGoodsQuery", pageParms, {
            name: value
        }).ok(json => {
            let result: any = json.result && json.result.content || [];
            this.suggestions = result || [];
        }).fail(err => {
            throw new Error(err);
        });
    }

}

@NgModule({
    declarations: [
        GoodSelectComponent
    ],
    exports: [GoodSelectComponent],
    imports: [
        CommonModule,
        FormsModule,
        AutoCompleteModule
    ]
})
export class GoodSelectModule { }



