import { Component, forwardRef, Input, OnInit, Output, EventEmitter } from "@angular/core";
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from "@angular/forms";
import { API } from "app/custom-components/services/api";


export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => ShipperSelectComponent),
    multi: true
};

@Component({
    templateUrl: "shipper-select.component.html",
    styleUrls: ["shipper-select.component.css"],
    selector: "shipper-select",
    providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class ShipperSelectComponent implements ControlValueAccessor,OnInit {
    /**
     * 
     */
    suggestions: any[];

    @Input()
    valueField: string = "";
    @Input()
    defaultLabel: string = "";
    @Input()
    multiSelect: boolean = false;
    @Input()
    width: string = "150px";
    @Input()
    height: string = "30px";

    @Output()
    onChange: EventEmitter<any> = new EventEmitter();
    

    public onTouchedCallback: () => () => {};
    public onChangeCallback: (_: any) => () => {};
    public innerValue;

    // 
    get value(): any {
        return this.innerValue;
    };

    // 
    set value(value: any) {
        let v = value && value.name?value.name:value;
        if (v !== this.innerValue) {
            if (this.valueField === 'object') {
                this.innerValue = value.label;
                this.onChangeCallback(value);
                this.onChange.emit(value);
            }else{
                this.innerValue = v;
                this.onChangeCallback(v);
                this.onChange.emit(v);
            }
        }
    }

    constructor(public api: API) {
    }

    ngOnInit() {
        this.suggestions = [];
    }

    // 
    writeValue(value: any) {
        if (value !== this.innerValue) {
            this.innerValue = value;
            //console.log(value);
        }
    }

    // 
    registerOnChange(fn: any) {
        this.onChangeCallback = fn;
    }

    // 
    registerOnTouched(fn: any) {
        this.onTouchedCallback = fn;
    }

    /**
     * 
     * @param $event
     */
    queryShipper($event: any) {
        let value = $event.query;
        this.api.call("customerWorkerController.queryShipperNameLike", {
            name: value,
            clientType:"send"
        }).ok(json => {
            let result: any = json.result || [];
            if(this.valueField === 'object'){
                this.suggestions=result;
                this.suggestions.forEach(item=>{
                    item.label=item.name;
                    item.name=item.name+' '+item.mobile;
                });
            }else{
                this.suggestions = result;
            }
        }).fail(err => {
            throw new Error(err);
        });
    }

}



