import { Component, forwardRef, Input, OnInit, NgModule, EventEmitter, Output } from "@angular/core";
import { ControlValueAccessor, NG_VALUE_ACCESSOR, FormsModule } from "@angular/forms";
import { API } from "app/custom-components/services/api";
import { CommonModule } from "@angular/common";
import { AutoCompleteModule } from "app/custom-components/_primeng-ext/autocomplete";


let noop = () => {
};

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => CneeSelectComponent),
    multi: true
};

@Component({
    templateUrl: "cnee-select.component.html",
    styleUrls: ["cnee-select.component.css"],
    selector: "cnee-select",
    providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class CneeSelectComponent implements ControlValueAccessor, OnInit {
    /**
     * 
     */
    suggestions: any[] = [];

    @Input()
    valueField: string = "";
    @Input()
    defaultLabel: string = "";
    @Input()
    multiSelect: boolean = false;
    @Input()
    width: string = "150px";
    @Input()
    height: string = "30px";

    @Output()
    onChange: EventEmitter<any> = new EventEmitter();

    public onTouchedCallback: () => void = noop;
    public onChangeCallback: (_: any) => void = noop;
    public innerValue;

    constructor(public api: API) {

    }

    ngOnInit() {
        this.suggestions = [];
    }


    // 
    get value(): any {
        return this.innerValue;
    };

    // 
    set value(value: any) {
        let v = value && value.name ? value.name : value;
        if (v !== this.innerValue) {
            if (this.valueField === 'object') {
                this.innerValue = value.label;
                this.onChangeCallback(value);
                this.onChange.emit(value)

            } else {
                this.innerValue = v;
                this.onChangeCallback(v);
                this.onChange.emit(v)
            }
        }
    }

    // 
    writeValue(value: any) {
        if (value !== this.innerValue) {
            this.innerValue = value;
        }
    }

    // 
    registerOnChange(fn: any) {
        this.onChangeCallback = fn;
    }

    // 
    registerOnTouched(fn: any) {
        this.onTouchedCallback = fn;
    }

    /**
     * 
     * @param $event
     */
    queryCnee($event: any) {
        let value = $event.query;
        this.api.call("customerWorkerController.queryShipperNameLike", {
            name: value,
            clientType: "receive"
        }).ok(json => {
            let result: any = json.result || [];
            if (this.valueField === 'object') {
                this.suggestions = result;
                this.suggestions.forEach(item => {
                    item.label = item.name;
                    item.name = item.name + ' ' + item.mobile;
                });
            } else {
                this.suggestions = result;
            }
        }).fail(err => {
            throw new Error(err);
        });
    }
}

@NgModule({
    declarations: [
        CneeSelectComponent
    ],
    exports: [CneeSelectComponent],
    imports: [
        CommonModule,
        FormsModule,
        AutoCompleteModule
    ]
})
export class CneeSelectModule { }


