import {
    Component,
    forwardRef,
    Input,
    OnInit,
    EventEmitter,
    Output,
    AfterViewInit,
    IterableDiffers,
    NgModule
} from "@angular/core";
import { ControlValueAccessor, NG_VALUE_ACCESSOR, FormsModule } from "@angular/forms";
import { API } from "app/custom-components/services/api";
import { CommonModule } from "@angular/common";
import { MultiSelectModule } from "app/components/multiselect/multiselect";
import { DropdownModule } from "app/components/dropdown/dropdown";


let noop = () => {
};

export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => RepairGoodsComponent),
    multi: true
};

@Component({
    templateUrl: "repair-goods-select.component.html",
    styleUrls: ["repair-goods-select.component.css"],
    selector: "repair-goods-select",
    providers: [CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class RepairGoodsComponent implements ControlValueAccessor,OnInit,AfterViewInit {

    repairGoods: any[] = [];

    @Input()
    defaultLabel: string = "";

    @Input()
    multiSelect: boolean = false;

    @Input()
    width: string = "150px";

    @Input()
    height: string = "";

    @Input()
    waybillId: string;

    @Input()
    styleClass: String;

    @Output()
    onChange: EventEmitter<any> = new EventEmitter<any>();

    @Output()
    onFocus: EventEmitter<any> = new EventEmitter<any>();

    @Output()
    onBlur: EventEmitter<any> = new EventEmitter<any>();

    public differ: any;

    public onTouchedCallback: () => void = noop;
    public onChangeCallback: (_: any) => void = noop;
    public innerValue;

    constructor(public api: API, differs: IterableDiffers) {

        this.differ = differs.find([]).create(null);
    }

    ngOnInit() {
        this.repairGoods = [];
    }

    ngAfterViewInit(): void {
        if (this.waybillId) {
            this.query(this.waybillId)
        }
    }
    // 
    get value(): any {
        return this.innerValue;
    };

    // 
    set value(v: any) {
        if (v !== this.innerValue) {
            this.innerValue = v;
            this.onChangeCallback(v);
        }
    }

    // 
    writeValue(value: any) {
        if (value !== this.innerValue) {
            this.innerValue = value;
        }
    }

    // 
    registerOnChange(fn: any) {
        this.onChangeCallback = fn;
    }

    // 
    registerOnTouched(fn: any) {
        this.onTouchedCallback = fn;
    }

    onChangeHandler(event) {
        let values = event.value;
        let selectedGoods = [];
        for (let v of values) {
            for (let item of this.repairGoods) {
                if (item.value === v) {
                    selectedGoods.push(item);
                }
            }
        }

        this.onChange.emit({
            event: event,
            selectedGoods: selectedGoods
        });
    }

    onFocusHandler(event) {
        this.onFocus.emit(event);
    }

    onBlurHandler(event) {
        this.onBlur.emit(event);
    }

    query(waybillId: string = "") {
        let queryParams = {
            "waybillId": waybillId
        };
        this.api.call("AbnormalRegistController.waybillQuery", queryParams).ok((json) => {
            let result: any = json.result || {};
            this.repairGoods = result.waybillGoodsInfo || [];
            this.repairGoods.forEach((repairGood) => {
                repairGood.label = repairGood["goodsName"];
                repairGood.value = repairGood["wayBillGoodsId"];
            });
        })
    }
}

@NgModule({
    declarations: [
        RepairGoodsComponent
    ],
    exports: [RepairGoodsComponent],
    imports: [
        CommonModule,
        FormsModule,
        MultiSelectModule,
        DropdownModule
    ]
})
export class RepairGoodsModule { }



