import { Injectable } from "@angular/core";
import { Http, Headers, RequestOptions } from "@angular/http";
import { Router } from "@angular/router";
import { Observable } from "rxjs";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/toPromise";
import 'rxjs/add/observable/throw';
import "rxjs/add/operator/map";
import { BASEURL } from "app/showcase/custom-components/config";

/**
 * APIControllerRestfulAPI
 */
@Injectable()
export class API {

    _url = BASEURL + '/api.do';
    /**
     * APINginx
     * @param url API
     */
    set url(url: string) {
        this._url = url;
    }

    /**
     * http
     * @param http
     */
    constructor(public http: Http, public router: Router) {
    }

    public call(...args: any[]): any {
        if (args.length < 1) {
            throw new Error("At least one argument for $rpc call ");
        }
        let name = args[0];
        args = args.slice(1);
        // Http
        let headers = new Headers({
            "Content-Type": "application/json"
        });
        // localStorage
        let jwt = localStorage["jwt"];
        if (jwt) {
            headers.append("Authorization", "Bearer " + jwt);
        }
        else {
            headers.append("Authorization", "Bearer anonymous.anonymous");
        }
        // http
        let options = new RequestOptions({ headers: headers });
        // 
        name = name.substring(0, 1).toLowerCase() + name.substring(1);
        let request = {
            name: name,
            token: localStorage.getItem("request-token"),
            args: args
        };
        // 
        let response = this.http.post(this._url, request, options)
            .map(res => res.json())
            .catch(error => (error && error.json()));
        // 
        let handlers = {};
        // 
        response.subscribe(json => {
            let ok = handlers["ok"];
            let fail = handlers["fail"];
            if (json["error"] || json["cause"]) {
                //
                if (json["code"] === -9999) {
                    localStorage.removeItem('currentUser');
                    localStorage.removeItem('jwt');
                    this.router.navigateByUrl("security/login");
                    return;
                }
                console.error(name + ":" + json["cause"]);
                if (fail instanceof Function) {
                    fail(json);
                }
            }
            else {
                if (ok instanceof Function) {
                    ok(json);
                }
            }
        }, error => {
            let fail = handlers["fail"];
            if (fail instanceof Function) {
                fail(error);
            }
        });
        // 
        let result = {
            ok: fn => {
                handlers["ok"] = fn;
                return result;
            },
            fail: fn => {
                handlers["fail"] = fn;
                return result;
            }
        };
        return result;
    }

    /**
     *  get
     * @param url
     * @param paramObj
     */
    public get(url: string, paramObj: any) {
        return this.http.get(url + this.toQueryString(paramObj))
            .toPromise()
        // .then(res => this.handleSuccess(res.json()))
        // .catch(error => this.handleError(error));
    }

    toQueryString(obj) {
        let ret = [];
        for (let key in obj) {
            if (obj.hasOwnProperty(key)) {
                key = encodeURIComponent(key);
                let values = obj[key];
                if (values && values.constructor === Array) {
                    let queryValues = [];
                    for (let i = 0, len = values.length, value; i < len; i++) {
                        value = values[i];
                        queryValues.push(this.toQueryPair(key, value));
                    }
                    ret = ret.concat(queryValues);
                } else {
                    //
                    ret.push(this.toQueryPair(key, values));
                }
            }
        }
        return '?' + ret.join('&');
    }

    toQueryPair(key, value) {
        if (typeof value === 'undefined') {
            return key;
        }
        return key + '=' + encodeURIComponent(value === null ? '' : String(value));
    }
}



